import BookSeriesItem from './BookSeriesItem.js';

const BookSeries = selectedData => {
  const webtoonSeries = new Array(7).fill('');

  return `
  <article class="books__series">
    <ul class="books__series__buy-tab">
      <!--  -->
      <li class="books__series__buy-tab__rental tab-list active">
        <h4 class="books__series__buy-tab__rental__title tab-title"></h4>
        <!--   -->
        <div class="list-option">
          <!-- list-option__left -->
          <div class="list-option__left">
            <label class="select-all"><input type="checkbox" /><span class="checkbox"></span> </label>
            <button type="button" class="alignment"><i class="bx bx-sort"></i></button>
          </div>

          <!-- list-option__right -->
          <div class="list-option__right">
            <span class="info_volume"> <span class="info_volume__count">0</span></span>
            <span class="info_price"><span class="info_price__num">0</span></span>
            <button type="button" class="cart"><i class="bx bxs-cart"></i></button>
            <button type="button" class="rental"> </button>
          </div>
        </div>
        <!--   -->
        <ul class="books__series__list">
          ${webtoonSeries.map((_, i) => BookSeriesItem(selectedData, i)).join('')}
        </ul>

        <button type="button" class="view-all-button"><i class="bx bxs-down-arrow"></i></button>
      </li>

      <!--  -->
      <li class="books__series__buy-tab__purchase tab-list ">
        <h4 class="books__series__buy-tab__purchase__title tab-title"></h4>
        <!--   -->
        <div class="list-option">
          <!-- list-option__left -->
          <div class="list-option__left">
            <label class="select-all"><input type="checkbox" /><span class="checkbox"></span> </label>
            <button type="button" class="alignment"><i class="bx bx-sort"></i></button>
          </div>

          <!-- list-option__right -->
          <div class="list-option__right">
            <span class="info_volume"> <span class="info_volume__count">0</span></span>
            <span class="info_price"><span class="info_price__num">0</span></span>
            <button type="button" class="cart"><i class="bx bxs-cart"></i></button>
            <button type="button" class="rental"> </button>
          </div>
        </div>
        <!--   -->
        <ul class="books__series__list">
          ${webtoonSeries.map((_, i) => BookSeriesItem(selectedData, i)).join('')}
        </ul>

        <button type="button" class="view-all-button"><i class="bx bxs-down-arrow"></i></button>
      </li>
    </ul>
  </article>
`;
};

export default BookSeries;
