import NewArrivalSectionItem from './NewArrivalSectionItem.js';

const NewArrivalSection = () => {
  // prettier-ignore
  const datas = [
    { title: '', iconClass: 'bx bx-book-open' },
    { title: '', iconClass: 'bx bx-bell' },
    { title: '', iconClass: 'bx bx-planet' },
    { title: '', iconClass: 'bx bx-time-five' },
    // { title: ' ', iconClass: 'bx bx-pin' },
    { title: '19+', iconClass: 'bx bx-heart' },
    { title: ' ', iconClass: 'bx bx-clipboard' },
    { title: ' ', iconClass: 'bx bx-cube' },
  ];

  // prettier-ignore
  return `
  <section class="new-arrival">
    <ul class="new-arrival__list">
    ${datas.map(data => `
      ${NewArrivalSectionItem(data)}
    `).join('')}
    </ul>
  </section>`;
};

export default NewArrivalSection;
