import MyRidiHomeItem from './MyRidiHomeItem.js';

const myAccountFirst = [
  { title: '', amount: '0', unit: '', link: ' ', icon: 'bx-dollar-circle', href: '/myridicharge' },
  { title: '', amount: '0', unit: '', link: ' ', icon: 'bxs-parking', href: '/mypage'},
  { title: '', amount: '11', unit: '', link: ' ', icon: 'bxs-coupon', href: '/mypage' },
];

const myAccountSecond = [
  { title: ' ', amount: '1', unit: '', link: ' ', icon: 'bx-book', href: '/mypage'},
  { title: '', amount: '0', unit: '', link: '', icon: 'bx-cart', href: '/mypage' },
  { title: '', amount: '0', unit: '', link: '', icon: 'bx-heart', href: '/mypage'},
];

const MyRidiHomeList = order => `
  <ul class="my__asset__table">
    ${
      order === 'first'
        ? myAccountFirst.map((item, i) => MyRidiHomeItem(item, order, i)).join('')
        : myAccountSecond.map(item => MyRidiHomeItem(item, order)).join('')
    }
  </ul>
`;
export default MyRidiHomeList;
