// eslint-disable-next-line import/no-named-as-default
import MyNavMenuSection from './MyNavMenuSection.js';

const myNavs= [
  { title: ' ', icon: 'home', href: ['/mypage'] }, 
  { title: '', subTitle: ['  '], icon: 'book-open', href: ['/recent'] },
  { title: '/', subTitle: [' ', ''], icon: 'credit-card', href: ['/payment', '/myridicharge'] }, 
  { title: '', subTitle: ['1:1 ', ' '], icon: 'user', href: ['/1on1inquiry', '/infochange'] }
];

const MyLeftMenu = href =>
  `
  <h3 class="my__left__menu__title"></h3>
  <nav class="my__nav__menu">
    ${myNavs.map((nav, i) => MyNavMenuSection(nav, i, href)).join('')}
  </nav>
`;

export default MyLeftMenu;
